n:10px auto}.button-wrapper{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:20px;padding:20px;font-family:sans-serif}.Alert{position:fixed;top:20px}.data-vis-wrapper{display:flex;align-items:center;flex-direction:column}.data-vis-wrapper .recharts-responsive-container{min-width:100%;display:flex}.recharts-surface{overflow:visible}.login-button{color:#494949;text-transform:uppercase;text-decoration:none;background:#ffffff;padding:20px;border:4px solid #494949;display:inline-block;transition:all 0.4s ease 0s;min-width:10rem;margin:1rem}.google-btn{width:184px;height:42px;margin:1rem;background-color:#4285f4;border-radius:2px;box-shadow:0 3px 4px 0 rgba(0,0,0,0.25)}.google-btn:hover{box-shadow:0 0 6px #4285f4}.google-btn:active{background:#1669f2}.google-icon-wrapper{position:absolute;margin-top:1px;margin-left:1px;width:40px;height:40px;border-radius:2px;background-color:#fff}.google-icon{position:absolute;margin-top:11px;margin-left:-8px;width:18px;height:18px}.btn-text{float:right;margin:11px 11px 0 0;color:#fff;font-size:14px;letter-spacing:0.2px;font-family:"Roboto"}.navbar{background:#fdfdff;box-shadow:0 0 10px #a6a6a6;color:#10166d;border:1px #10166d;border-radius:5px;position:fixed;-webkit-transform:translateX(-12%);transform:translateX(-12%);top:0;width:130%;height:65px;z-index:999}@media only screen and (min-width: 992px){.data-vis-wrapper{flex-direction:row}.data-vis-wrapper .recharts-responsive-container{flex-basis:50%;max-width:50%;min-width:auto;margin:0 auto}}@media only screen and (max-width: 1200px){.EventList{grid-template-columns:1fr;grid-gap:15px}}@media only screen and (max-width: 500px){.summary{font-size:30px}.attend-btn{position:absolute;left:12%}}

